body {
	background: #EBE0CC;
	font-family: "Poppins", sans-serif;
	overflow: scroll;
}

.logo {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.logo img {
	height: 70%;
	transition: 1s;
}
.logo img:hover {
	transform: rotate(360deg);
}

.nav {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-size: 1.7vw;
	letter-spacing: 0.05em;
}
@media screen and (max-width: 576px) {
	.nav {
		font-size: 4vw;
	}
}

.faq-button {
	color: black;
	text-decoration: none;
}

.nav-dropdown {
	position: relative;
	cursor: pointer;
}
.nav-dropdown:hover ul {
	display: block;
}
.nav-dropdown ul {
	position: absolute;
	display: none;
	top: 100%;
	left: 0;
	width: 100%;
	background-color: #ebe0cc;
}
.nav-dropdown ul li {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border: 1px solid #202020;
	border-bottom-width: 0;
	font-size: 0.8em;
	line-height: 1.2em;
	text-align: center;
	min-height: 6.25vw;
	padding: 0 1vw;
	color: #202020;
	text-decoration: none;
	cursor: pointer;
}
.nav-dropdown ul li:last-child {
	border-bottom-width: 1px;
}
.nav-dropdown ul li:hover {
	background-color: rgba(255, 255, 255, 0.3);
}
.nav-dropdown ul li.active {
	background-color: rgba(255, 255, 255, 0.5);
}
.nav-dropdown ul li.clicked {
	background-color: white;
}
.nav-dropdown ul li small {
	display: block;
	font-size: 0.8em;
	opacity: 0.5;
}

.filled-spacer {
	background-color: #620505;
}

.main-title {
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 0 0 0 2vw;
}
@media screen and (max-width: 576px) {
	.main-title {
		padding: 0 0 0 6vw;
	}
}
.main-title h1 {
	font-weight: 300;
	font-size: 11vw;
}
@media screen and (max-width: 576px) {
	.main-title h1 {
		font-size: 20vw;
	}
}
.main-title h1 span:nth-child(2) {
	font-size: 0.85em;
}
.main-title h1 span:nth-child(3) {
	font-size: 0.7em;
}
.main-title h1 span {
	transition: letter-spacing 0.3s;
}
.main-title h1 span:hover {
	letter-spacing: 0.03em;
}

.main-title-mobile {
	text-align: center;
	display: flex;
	justify-content: center;
	flex-direction: column;
}
.main-title-mobile.main-title-mobile-student {
	font-size: 13vw;
	text-transform: uppercase;
	letter-spacing: 0.1em;
}
.main-title-mobile.main-title-mobile-portfolio {
	font-size: 20vw;
	letter-spacing: 0.1em;
}
.main-title-mobile.main-title-mobile-review {
	font-size: 14vw;
	text-transform: uppercase;
	letter-spacing: 0.1em;
}

.big-letter {
	position: relative;
	font-size: 9vw;
	text-transform: uppercase;
	text-align: center;
	overflow: hidden;
}
.big-letter span {
	position: absolute;
	top: 50%;
	width: 100%;
	transform: translate(-50%, -50%);
}
.big-letter.animate span {
	animation-name: big-letter-right;
	animation-duration: 1s;
	animation-fill-mode: both;
}
.big-letter.big-letter-c1.animate span {
	animation-name: big-letter-down;
}
.big-letter.big-letter-i.animate span {
	animation-name: big-letter-up;
}
.big-letter.big-letter-r.animate span {
	animation-name: big-letter-left;
}
.big-letter.big-letter-u.animate span {
	animation-name: big-letter-left;
}
.big-letter.big-letter-s.animate span {
	animation-name: big-letter-down;
}

.star-line {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}
.star-line img {
	height: 50%;
}
.star-line.star-line-corner img {
	height: 25%;
}

.featured-person {
	background-size: contain;
	background-position: bottom center;
	background-repeat: no-repeat;
	position: relative;
	overflow: hidden;
}
.featured-person.featured-person-orange {
	background-color: #db7d42;
}
.featured-person.featured-person-blue {
	background-color: #6889aa;
}
.featured-person.featured-person-peach {
	background-color: #ebb29d;
}
.featured-person img {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	transform: translateX(110%);
}
.featured-person img:nth-child(1) {
	transform: translateX(0%);
}

.spacer-top-of-students {
	display: flex;
}
.spacer-top-of-students div {
	flex-grow: 1;
	display: flex;
	justify-content: center;
	align-items: center;
}
.spacer-top-of-students div img {
	height: 15%;
}

.student {
	display: flex;
	flex-direction: column;
}
@media screen and (max-width: 576px) {
	.student:nth-child(odd) figcaption {
		order: -1;
	}
}
.student.student-purple figure {
	background-color: #ac9deb;
}
.student.student-pink figure {
	background-color: #eb9daf;
}
.student.student-blue figure {
	background-color: #89a5d3;
}
.student.student-green figure {
	background-color: #68aa81;
}
.student.student-orange figure {
	background-color: #d1986e;
}
.student.student-yellow figure {
	background-color: #dbbe42;
}
.student.student-dark-blue figure {
	background-color: #6877aa;
}
.student .student-content {
	width: 100%;
	display: flex;
}
.student .student-content figure {
	width: 50%;
	position: relative;
	height: 0;
	padding-bottom: 50%;
}
@media screen and (max-width: 576px) {
	.student .student-content figure {
		width: 58.3333333333%;
		padding-bottom: 58.3333333333%;
	}
}
.student .student-content figure img {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	display: none;
}
.student .student-content figure img:nth-child(1) {
	display: block;
}
.student .student-content figure img.hide {
	display: none !important;
}
.student .student-content figure img.show {
	display: block !important;
}
.student .student-content figcaption {
	width: 50%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	font-size: 2vw;
	padding: 1vw 1vw;
}
@media screen and (max-width: 576px) {
	.student .student-content figcaption {
		padding: 1vw 2vw;
		width: 41.6666666667%;
		font-size: 4vw;
	}
}
.student .student-content figcaption h3 {
	font-size: 1.4em;
	margin: 0.2em 0;
}
.student .student-content figcaption h4 {
	font-size: 0.95em;
	margin: 0.15em 0 0.2em;
	font-style: italic;
}
.student .student-content figcaption h5 {
	font-size: 0.65em;
	margin: 0.5em 0 0.25em;
}
.student .student-content figcaption h6 {
	font-size: 0.65em;
	margin: 0 0;
}
.student .student-content figcaption p {
	margin: 0.5em 0;
}
.student .student-content figcaption p a {
	display: inline-block;
	font-size: 0.6em;
	padding: 0.68em 0.9em 0.52em;
	margin-top: 0.5em;
	border: 1px solid #202020;
	border-radius: 100px;
	color: #202020;
	text-decoration: none;
}
.student .student-content figcaption p a:hover {
	background-color: rgba(255, 255, 255, 0.5);
}
.student .spacer {
	width: 100%;
	flex-grow: 2;
	display: flex;
}
.student .spacer div {
	display: flex;
	position: relative;
	flex-grow: 1;
	border-top: 1px solid #202020;
	border-left: 1px solid #202020;
}
.student .spacer div img {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	height: 25%;
}
.student .spacer div:nth-child(1) {
	border-left-width: 0;
}
.student .spacer.spacer-2-1 div:nth-child(1) {
	flex-grow: 2;
}
.student .spacer.spacer-1-2 div:nth-child(2) {
	flex-grow: 2;
}

.end-cap {
	display: flex;
	justify-content: center;
	align-items: center;
}
.end-cap a {
	font-size: 1.6em;
	color: black;
	text-decoration: none;
}
.end-cap a:hover span.text {
	text-decoration: underline;
}
.end-cap a:hover span.bouncer {
	position: relative;
	animation-name: bounce-arrow;
	animation-duration: 1s;
	animation-timing-function: linear;
	animation-iteration-count: infinite;
}

.faq .faq-content {
	width: 66.6666666667%;
	margin: 0 auto;
	padding: 2em 1em;
}
.faq .faq-content h2 {
	font-size: 4em;
	letter-spacing: 0.2em;
}
.faq .faq-content ul {
	list-style-type: none;
	margin: 0;
}
.faq .faq-content ul li {
	margin: 1.5em 0 2em;
}
.faq .faq-content ul li p {
	margin: 0.5em 0;
	line-height: 1.4em;
}
.faq .faq-content ul li p.q {
	font-size: 1.25em;
	font-style: italic;
}
.faq .faq-content ul li p a {
	color: black;
}

